@import './Sass/GlobalFunctions.module.scss';

html {
  height: 100%;
}

body {
  height: 100%;
}

#root {
  height: 100%;
}

.App {
  min-height: 100%;
  overflow-x: hidden;
  background-color: f_color(primary);
}

.ModalWrap {
  height: 100vh;
  pointer-events: none;
  position: fixed;
  width: 100vw;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: center;
}